<template>
  <el-dialog
    :title="title"
    :visible.sync="is_visible"
    :width="`${((columns.length<7?840:columns.length*120)>1200?1200:(columns.length<5?840:columns.length*120))+20}px`"
    @close="on_close"
    :append-to-body="false"
    style="overflow: auto"
  >
    <el-table
      :data="data"
      stripe
      border
      size="mini"
      v-loading="loading"
      @sort-change="on_change_sort"
      @filter-change="on_change_filter"
    >
      <el-table-column
        v-for="(item, index) in columns"
        :key="item"
        :prop="item"
        sortable
        min-width="120"
        :formatter="format_row"
        :label="`${item} ${verbose_names[index]}`"
        :show-overflow-tooltip="true"
      >
        <template slot-scope="scope">
          <el-button
            v-if="Object.keys(graph.detail_dialog_settings).includes(item)"
            type="text"
            size="mini"
            @click="on_show_detail(scope.row, item)"
          >{{ scope.row[item] }}</el-button>
          <span v-else>
            {{ scope.row[item] }}
          </span>
        </template>
      </el-table-column>
    </el-table>
    <div style="height: 80px">
      <el-pagination
        @current-change="get_data()"
        :current-page.sync="params.page"
        :page-size="params.page_size"
        style="float: right; margin-right: 100px; margin-top: 20px"
        background
        layout="total, prev, pager, next"
        :total="count"
      >
      </el-pagination>
    </div>
  </el-dialog>
</template>

<script>
import list_mixin from "../../../utils/mixins/list_mixin";
import dialog_mixin from "../../../utils/mixins/dialog_mixin";
export default {
  name: "dfReportList",
  mixins: [dialog_mixin, list_mixin],
  props: ["graph", "click_args", "title", "p_params", "filter_params", "total_count"],
  data() {
    return {
      url: `report/graphs/${this.graph.id}/get-detail-list`,
      extend_params: Object.assign({}, this.p_params),
      columns: [],
      verbose_names: []
    };
  },
  mounted() {
    this.get_data();
  },
  methods: {
    format_row(item, column, cell_alue, index) {
      if (typeof cell_alue == "boolean") {
        return String(cell_alue);
      } else if (typeof cell_alue == "object") {
        return JSON.stringify(cell_alue);
      }
      return cell_alue;
    },
    get_data() {
      this.loading = true;
      this.$c_master
        .post(
          this.url,
          { click_args: this.click_args, filters: this.filter_params.filters },
          { params: this.params }
        )
        .then(response => {
          this.loading = false;
          this.data = response.data.results;
          this.columns = response.data.columns;
          this.verbose_names = response.data.verbose_names;
          this.count = response.data.count;
        })
        .catch(error => {
          this.loading = false;
          console.error("graph get_detail_list_data error: ", error);
        });
    },
    on_show_detail(item, key) {
      var props = {};
      props[this.graph.detail_dialog_settings[key].field_name] = item[key];
      this.add_dialog(
        this.graph.detail_dialog_settings[key].component_name,
        props
      );
    }
  }
};
</script>



