<template>
  <el-submenu
    v-if="display_count > 0"
    :index="item.name"
  >
    <template slot="title">
      <i
        class="fa"
        :class="item.meta.icon"
        style="margin-right: 5px"
      ></i>
      <span style="display: inline-block; width: 200px">{{ item.meta.name }}</span>
    </template>
    <el-menu-item
      v-for="(child_item, index) in item.children"
      v-if="!child_item.meta || child_item.meta.display!=false"
      :key="index"
      :index="child_item.name"
    >
      <i
        class="fa"
        :class="child_item.meta.icon"
        style="margin-right: 5px"
      ></i>
      <span
        slot="title"
        style="display: inline-block; width: 140px"
      >{{ child_item.meta.name }}</span>
    </el-menu-item>
  </el-submenu>
  <el-menu-item
    v-else
    :index="item.name"
  >
    <i
      class="fa"
      :class="item.meta.icon"
      style="margin-right: 5px"
    ></i>
    <span
      slot="title"
      style="display: inline-block; width: 200px"
    >{{ item.meta.name }}</span>
  </el-menu-item>
</template>


<script>
import base from "../../base";
export default {
  name: "dfMenuItem",
  extends: base,
  props: ["item"],
  computed: {
    display_count() {
      var count = 0;
      if (!this.item.children || this.item.children.length == 0) {
        return count;
      }
      for (let item of this.item.children) {
        if (item.meta && item.meta.display != false) {
          count += 1;
        }
      }
      return count;
    }
  }
};
</script>


