<template>
  <el-date-picker
    v-model="inner_value"
    @change="on_change"
    :size="size"
    :picker-options="picker_options"
    :default-time="['00:00:00', '23:59:59']"
    :type="type"
    :style="{width: `${width_map[type]}px`}"
    :start-placeholder="startPlaceholder"
    :end-placeholder="endPlaceholder"
  >
  </el-date-picker>
</template>

<script>
import moment from "moment";
import base from "../base"
export default {
  name: "dfDateRange",
    extends: base,
  props: {
    value: {
      type: Object,
      required: true
    },
    label: {
      type: String,
      default: ""
    },
    start_field: {
      type: String,
      required: true
    },
    end_field: {
      type: String,
      required: true
    },
    size: {
      type: String,
      default: "medium"
    },
    startPlaceholder: {
      type: String,
      default: "开始日期"
    },
    endPlaceholder: {
      type: String,
      default: "结束日期"
    },
    type: {
      type: String,
      default: "daterange",
      validator: value => {
        return ["daterange", "datetimerange"].includes(value);
      }
    }
  },
  data() {
    return {
      inner_value: [this.value[this.start_field], this.value[this.end_field]],
      width_map: {
        daterange: 260,
        datetimerange: 390
      },
      picker_options: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = moment()
                .endOf("day")
                .toDate();
              const start = moment()
                .add(1, "day")
                .startOf("day")
                .subtract(1, "weeks")
                .toDate();
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = moment()
                .endOf("day")
                .toDate();
              const start = moment()
                .add(1, "day")
                .startOf("day")
                .subtract(1, "months")
                .toDate();
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = moment()
                .endOf("day")
                .toDate();
              const start = moment()
                .add(1, "day")
                .startOf("day")
                .subtract(3, "months")
                .toDate();
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      }
    };
  },
  methods: {
    on_change(values) {
      if (values) {
        this.value[this.start_field] = values[0];
        this.value[this.end_field] = values[1];
      } else {
        this.value[this.start_field] = "";
        this.value[this.end_field] = "";
      }
      this.$emit("change", values);
    }
  }
};
</script>


