<template>
  <el-select
    v-model="inner_value"
    style="width: 100%"
    filterable
    remote
    :reserve-keyword="reserveKeyword"
    :clearable="clearable"
    :multiple="multiple"
    :placeholder="placeholder"
    :remote-method="on_query"
    :size="size"
    :value-key="value_key"
    :loading="query_loading"
    :disabled="disabled"
    :allow-create="allowCreate"
  >
    <slot></slot>
    <el-option
      v-for="(item, index) in options"
      :key="value_field==undefined?index:item[value_field]"
      :label="option_formatter?option_formatter(item, index):(Array.isArray(display_field)?display_field.map((key)=>item[key]).join(' | '):item[display_field])"
      :value="value_field==undefined?item:item[value_field]"
      :disabled="whether_disable(item)"
    >
    </el-option>
    <el-button
      v-if="!disable_query"
      type="text"
      :disabled="count<inner_params.page_size"
      @click="load_more"
      size="small"
      style="width: 100%; color: #9A9A9A"
      :loading="more_loading"
    >
      加载更多
    </el-button>
  </el-select>
</template>

<script>
import axios from "axios";
export default {
  name: "dfRemoteQuerySelect",
  props: {
    value: {},
    placeholder: {
      default: "输入关键字搜索"
    },
    multiple: {
      type: Boolean,
      default: false
    },
    clearable: {
      type: Boolean,
      default: false
    },
    url: {
      type: String,
      required: false
    },
    params: {
      type: Object,
      default: () => {
        return {};
      }
    },
    results_field: {
      default: "results"
    },
    value_field: {
      type: String,
      required: false
    },
    display_field: {
      required: false
    },
    disabled_field: {
      type: String,
      required: false
    },
    disabled_func: Function,
    size: {
      type: String,
      default: "medium"
    },
    reserveKeyword: {
      type: Boolean,
      default: false
    },
    value_key: {
      type: String,
      default: "value"
    },
    auto_select_first: {
      type: Boolean,
      default: false
    },
    force_auto_select_first: {
      type: Boolean,
      default: false
    },
    disabled: Boolean,
    disable_query: Boolean,
    option_formatter: Function,
    allowCreate: Boolean
  },
  data() {
    return {
      inner_value: this.value,
      // inner_value: [1, 2],
      options: [],
      filtered_options: [],
      query_loading: false,
      more_loading: false,
      count: 0,
      inner_params: {
        page_size: 10,
        page: 1,
        search: ""
      },
      source: axios.CancelToken.source(),
      first_get_options: true
    };
  },
  mounted() {
    if (!this.disable_query && this.value_field == undefined && this.value_key == undefined) {
      throw Error("prop 参数 value_field 和 key_field 不能同时为空 ");
    }
    if (!this.disable_query && this.display_field == undefined && this.option_formatter == undefined) {
      throw Error("prop 参数 display_field 和 option_formatter 不能同时为空 ");
    }
    this.init_options();
  },
  methods: {
    init_options() {
      if (
        this.value_field == undefined &&
        this.multiple &&
        Array.isArray(this.inner_value) &&
        this.inner_value.length > 0
      ) {
        this.options = Object.assign(this.inner_value);
        this.get_data();
        return;
      } else if (
        this.value_field == undefined &&
        !this.multiple &&
        typeof this.inner_value == "object"
      ) {
        this.options = [this.inner_value];
        this.get_data();
        return;
      }
      var init_values = [];
      if (Array.isArray(this.inner_value)) {
        init_values = this.inner_value;
      } else if (this.inner_value) {
        init_values = [this.inner_value];
      }
      if (init_values.length && !this.disable_query) {
        var requests = init_values.map(item =>
          this.$c_master.get(`${this.url}/${item}`)
        );
        axios.all(requests).then(
          axios.spread((...responses) => {
            this.options = responses.map(item => item.data);
            this.get_data(true);
          })
        );
      } else {
        this.get_data();
      }
    },
    get_data(add = false) {
      this.source.cancel("有新查询 取消老的查询");
      this.source = axios.CancelToken.source();
      if(this.disable_query){
        return
      }
      if (add) {
        this.more_loading = true;
      } else {
        this.query_loading = true;
      }
      var params = Object.assign({}, this.inner_params, this.params);
      this.$c_master
        .get(this.url, { params: params, cancelToken: this.source.token })
        .then(response => {
          if (add) {
            this.more_loading = false;
            this.options = this.options.concat(
              response.data[this.results_field]
            );
          } else {
            this.query_loading = false;
            this.options = response.data[this.results_field];
          }
          this.count = response.data[this.results_field].length;
          console.log(
            "this.force_auto_select_first: ",
            this.force_auto_select_first
          );
          if (
            this.auto_select_first &&
            this.first_get_options &&
            this.options.length
          ) {
            console.log("in...");
            if (
              this.multiple &&
              (this.force_auto_select_first || this.inner_value.length == 0)
            ) {
              this.inner_value = [
                this.value_field == undefined
                  ? this.options[0]
                  : this.options[0][this.value_field]
              ];
            } else if (
              this.force_auto_select_first ||
              [undefined, null, ""].includes(this.inner_value)
            ) {
              this.inner_value =
                this.value_field == undefined
                  ? this.options[0]
                  : this.options[0][this.value_field];
            }
          }
          console.log("this.inner_value: ", this.inner_value);
          this.first_get_options = false;
        })
        .catch(error => {
          if (add) {
            this.more_loading = false;
          } else {
            this.query_loading = false;
          }
        });
    },
    on_query(query) {
      if (this.disable_query) {
        return;
      }
      this.inner_params.search = query;
      this.inner_params.page = 1;
      this.get_data();
    },
    load_more() {
      this.inner_params.page += 1;
      this.get_data(true);
    },
    whether_disable(item) {
      if (this.disabled_func) {
        return this.disabled_func(item);
      }
      return this.disabled_field ? Boolean(item[this.disabled_field]) : false;
    },
    clean_options() {
      this.options = [];
    }
  },
  watch: {
    value(new_value, old_value) {
      if (new_value === old_value) {
        return;
      }
      this.inner_value = new_value;
    },
    inner_value(new_value) {
      this.$emit("input", new_value);
      this.$emit("change", new_value);
    },
    url() {
      this.inner_params.page = 1;
      this.get_data();
    }
  }
};
</script>



