<template>
  <div style="height: 100%; display: flex">
    <el-menu
      style="height: 100%; display: inline-block"
      :default-active="$route.name"
      unique-opened
      @select="on_select"
      :collapse="is_collapse"
    >
      <df-menu-item
        v-for="(item, index) in menus"
        v-if="!item.meta || item.meta!='false'"
        :key="index"
        :item="item"
      >
      </df-menu-item>
    </el-menu>
    <div style="height: 100%; display: inline-block; display: flex; align-items: center">
      <i
        @click="is_collapse=!is_collapse"
        style="color: #909399; cursor: pointer;"
        class="fa fa-outdent"
        :class="{'fa-rotate-180': is_collapse}"
      ></i>
    </div>
  </div>
</template>

<script>
export default {
  name: "dfMenu",
  props: {
    // 默认是否折叠菜单栏
    collapse: {
      type: Boolean,
      default: false
    },
    /*菜单
     [{
        path: "admin",
        name: "admin",
        component: _import("Blank"),
        meta: {
            name: "管理员",
            icon: "fa-lock",
            permissions: ["admin"]
        },
        children: [
            {
                path: "admin-list",
                name: "admin-list",
                component: _import("admin/List"),
                meta: {
                    name: "所有事件",
                    icon: "fa-th-large",
                }
            },
            {
                path: "irecord",
                name: "irecord",
                component: _import("admin/IRecord"),
                meta: {
                    name: "我记录的",
                    icon: "fa-th-large",
                }
            },
            {
                path: "address-book",
                name: "address-book",
                component: _import("admin/AddressBook"),
                meta: {
                    name: "通讯录",
                    icon: "fa-th-large",
                    permissions: ["admin"]
                }
              },
            ]
          }
        ]
     */
    menus: {
      type: Array,
      default: () => {
        return [];
      }
    }
  },
  data() {
    return {
      is_collapse: this.collapse,
      menu_map: {}
    };
  },
  mounted() {
    this.init_menu_map(this.menus);
    if (
      Object.keys(this.menu_map).includes(this.$route.name) &&
      this.menu_map[this.$route.name].meta &&
      this.menu_map[this.$route.name].meta.collapse === true
    ) {
      this.is_collapse = true;
    }
  },
  methods: {
    on_select(key, path) {
      var item = this.menu_map[key];
      if (item.meta && item.meta.collapse === true) {
        this.is_collapse = true;
      }
      this.$router.push({ name: key });
    },
    folding() {
      this.is_collapse = false;
    },
    unfolding() {
      this.is_collapse = true;
    },
    init_menu_map(menus) {
      for (let item of menus) {
        this.menu_map[item.name] = item;
        if (item.children) {
          this.init_menu_map(item.children);
        }
      }
    }
  }
  // components: {
  //   DfMenuItem
  // }
};
</script>

