

<template>
  <div style="border: 1px solid #EEEEEE; border-radius: 2px">
    <l-table
      v-if="graph._type==85"
      :table_data="data.rows"
      :columns="columns"
      :row="row"
      :graph="graph"
      @delete="$emit('delete_graph')"
      @cell-click="on_click_graph"
    ></l-table>
    <div v-else>
      <div style="display: flex; justify-content: space-between">
        <div style="font-size: 24px; font-weight: 600">{{ graph.name }}</div>
        <div>
          <i
            class="el-icon-delete"
            @click="on_delete_graph"
          ></i>
          <i
            class="el-icon-edit-outline"
            @click="on_update_graph"
          ></i>
        </div>
      </div>
      <component
        :is="type_map[graph._type]"
        :loading="loading"
        :data="data"
        :events="events"
        :settings="settings"
        :toolbox="toolbox"
        :extend="extend"
        legend-position="bottom"
        :height="`${graph.height}px`"
      >
        <div
          v-show="is_data_empty"
          class="data-empty"
        >没有数据 扩大时间范围试试。。。</div>
      </component>
    </div>
  </div>
</template>

<script>
import chart_mixin from "../../../utils/mixins/chart_mixin";
import l_table from "./Table";
export default {
  name: "dfReportGraph",
  components: {
    lTable: l_table,
  },
  mixins: [chart_mixin],
  props: ["graph", "datetime_range", "filter_params", "row"],
  data() {
    this.type_map = {
      80: "VeLine",
      65: "VeBar",
      50: "VeHistogram",
      20: "VePie",
      19: "VeRing",
      18: "VeFunnel",
      17: "VeWaterfall",
      16: "VeRadar",
      15: "VeMap",
      14: "VeSankey",
      13: "VeHeatmap",
      12: "VeScatter",
      11: "VeCandle",
      10: "VeGauge",
      9: "VeTree"
    };
    var waterMarkText = `${this.$store.state.user_info.username}-${
      this.$store.state.user_info.name
    }`;
    var canvas = document.createElement("canvas");
    var ctx = canvas.getContext("2d");
    canvas.width = canvas.height = 300;
    ctx.textAlign = "center";
    ctx.textBaseline = "middle";
    ctx.globalAlpha = 0.08;
    ctx.font = "30px Microsoft Yahei";
    ctx.translate(130, 130);
    ctx.rotate(-Math.PI / 4);
    ctx.fillText(waterMarkText, 0, 0);
    this.toolbox = {
      feature: {
        magicType: { type: ["line", "bar"] },
        saveAsImage: {},
        dataView: {},
        dataZoom: {}
      }
    };
    return {
      url: `report/graphs/${this.graph.id}/get-data`,
      params: {
        start_time: this.datetime_range[0],
        end_time: this.datetime_range[1]
      },
      settings: Object.assign({}, this.graph.settings, {
        dimension: [this.graph.dimension],
        metrics: this.graph.metrics,
        label: {
          formatter(args) {
            if (["pie", "ring", "funnel"].includes(args.seriesType)) {
              return `${args.name} | ${args.value}`;
            }
            return args.name;
          }
        }
      }),
      extend: Object.assign(
        {
          backgroundColor: {
            type: "pattern",
            image: canvas,
            repeat: "repeat"
          }
        },
        this.graph.extend
      ),
      events: {
        click: event => {
          console.log(event)
          if (!this.graph.on_click) {
            this.$message.warning("没有配置详情列表 请联系管理员配置");
            return;
          }
          var click_args = {
            dimension: event.name,
            metric: event.seriesName,
            count: event.value,
            item_data: this.data.rows[event.dataIndex]
          };
          var title = event.name;
          if (this.settings.metrics.length > 1) {
            title += `&&${event.seriesName}`;
          }
          this.on_click_graph(click_args, title);
        }
      },
      columns: []
    };
  },
  mounted() {
    this.get_data();
  },
  methods: {
    on_update_graph() {
      var vm = this.$df_add_dialog("dfReportCreateUpdateGraph", {
        row: this.row,
        type: "update",
        item: this.graph
      });
      vm.$on("refresh", item => {
        Object.assign(this.graph, item);
        this.get_data();
      });
    },
    on_delete_graph() {
      const self = this;
      this.$confirm(this.graph.name, "警告", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        beforeClose(action, instance, done) {
          if (action === "confirm") {
            instance.confirmButtonLoading = true;
            self.delete_graph(instance, done);
          } else {
            done();
          }
        }
      });
    },
    get_data() {
      this.loading = true;
      this.$c_master
        .post(this.url, this.filter_params, { params: this.params })
        .then(response => {
          this.loading = false;
          this.data.rows = response.data.results;
          this.columns = response.data.columns;
        })
        .catch(error => {
          this.loading = false;
          console.error("graph get_data error: ", error);
        });
    },
    on_change(start_time, end_time) {
      console.log("graph on change");
      this.params.start_time = start_time;
      this.params.end_time = end_time;
      this.get_data();
    },
    delete_graph(instance, done) {
      const url = `report/graphs/${this.graph.id}`;
      this.$c_master
        .delete(url)
        .then(response => {
          instance.confirmButtonLoading = false;
          this.$emit("delete_graph");
          this.$message.success("已删除");
          done();
        })
        .catch(error => {
          instance.confirmButtonLoading = false;
        });
    },
    on_click_graph(click_args, title) {
      if (!this.graph.on_click) {
        console.log("没有配置详情列表 无法响应点击事件");
        return;
      }
      this.$df_add_dialog("dfReportList", {
        graph: this.graph,
        click_args: click_args,
        title: title,
        p_params: this.params,
        filter_params: this.filter_params,
        total_count: click_args.count,
      });
    }
  }
};
</script>



<style scoped>
.data-empty {
  position: absolute;
  left: 0;
  right: 0;
  top: 30px;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.7);
  color: #888;
  font-size: 14px;
}
</style>


