<template>
  <el-col
    :xs="12"
    :sm="12"
    :lg="6"
  >
    <div
      style="height: 108px; margin: 0px 20px; border: 1px solid #ebebeb; border-radius: 3px; cursor: pointer"
      :style="{cursor: router_name?'pointer':'auto'}"
      @click="on_click"
    >
      <div style="float: left; margin-top: 30px; margin-left: 30px">
        <i
          class="fa fa-3x"
          v-bind:class="icon"
          style="color: #40c9c6"
        ></i>
      </div>
      <div style="float: right; margin-top: 26px; margin-right: 30px">
        <div style="font-size: 16px; font-weight: 700; color: rgb(140, 140, 140); margin-bottom: 12px">{{ name }}</div>
        <div
          v-loading="loading"
          element-loading-spinner="el-icon-loading"
          style="font-size: 20px; font-weight: 700; color: #666"
        >{{ l_count }}</div>
      </div>
    </div>
  </el-col>
</template>

<script>
import base from "../base";
export default {
  name: "dfCartPanel",
  extends: base,
  props: {
    icon: {
      type: String,
      required: true
    },
    name: {
      type: String,
      required: true
    },
    count: {
      required: false
    },
    router_name: {
      default: null
    },
    router_params: {
      type: Object,
      default: () => {}
    },
    url: {
      type: String,
      required: false
    },
    params: {
      type: Object,
      default: () => {
        return {};
      }
    },
    value_field: {
      type: String,
      defautl: "count"
    }
  },
  data() {
    return {
      loading: false,
      l_count: this.count ? this.count : null
    };
  },
  mounted() {
    if (this.url) {
      this.get_data();
    }
  },
  methods: {
    on_click() {
      if (this.router_name) {
        this.$router.push({
          name: this.router_name,
          params: this.router_params
        });
      }
    },
    get_data() {
      this.loading = true;
      this.l_axios
        .get(this.url, { params: this.params })
        .then(response => {
          this.loading = false;
          this.l_count = response.data[this.value_field];
        })
        .catch(error => {
          this.loading = false;
          console.error("cart-panel get_data error: ", error);
        });
    }
  }
};
</script>

<style scoped>
</style>



