<template>
  <div>
    <el-row :gutter="20">
      <el-col
        v-for="(item, index) in data"
        :key="index"
        :span="item.span"
      >
        <df-report-graph
          :graph="item"
          :datetime_range="datetime_range"
          :filter_params="filter_params"
          :row="row"
          @delete_graph="on_delete_graph(item)"
          ref="graphs"
        ></df-report-graph>
      </el-col>
    </el-row>
    <div>
      <el-tooltip content="添加图到当前行">
        <i
          class="fa fa-plus-circle"
          aria-hidden="true"
          @click="on_add_graph"
        ></i>
      </el-tooltip>
      <div style="float: right">
        <el-tooltip content="移除当前Row">
            <i
              class="el-icon-remove"
              aria-hidden="true"
              @click="on_delete()"
            ></i>
        </el-tooltip>
        <el-tooltip content="编辑当前Row" style="margin-left: 4px">
          <i
            class="fa fa-pencil-square"
            aria-hidden="true"
            @click="$df_add_dialog('dfReportCreateUpdateRow', {type: 'update', item: row })"
          ></i>
        </el-tooltip>
      </div>
    </div>
  </div>
</template>

<script>
import list_mixin from "../../../utils/mixins/list_mixin";
export default {
  name: "dfReportRow",
  mixins: [list_mixin],
  props: ["row", "datetime_range", "filter_params"],
  data() {
    return {
      url: "report/graphs",
      params: {
        row: this.row.id,
        ordering: "-priority"
      }
    };
  },
  mounted() {
    this.get_data();
  },
  methods: {
    on_change(start_time, end_time, filter_params) {
      console.log("row refs graphs:", this.$refs.graphs);
      if (!this.$refs.graphs) {
        return;
      }
      this.$refs.graphs.forEach(item => {
        item.on_change(start_time, end_time, filter_params);
      });
    },
    on_add_graph() {
      var vm = this.$df_add_dialog("dfReportCreateUpdateGraph", {
        row: this.row
      });
      vm.$on("refresh", () => {
        this.get_data();
      });
    },
    on_delete_graph(item) {
      this.data.splice(this.data.indexOf(item), 1);
    },
    on_delete(){
      const url = `report/rows/${this.row.id}`;
      this.loading = true;
      this.$c_master
        .delete(url)
        .then(reponse => {
          this.loading = false;
          this.$message.success("已删除");
          this.$emit("delete")
        })
        .catch(error => {
          this.loading = false;
        });
    }
  }
  // updated() {
  //   console.log("Row updated this.$refs.graphs:", this.$refs.graphs);
  //   if (!this.$refs.graphs) {
  //     return;
  //   }
  //   this.$refs.graphs.forEach(item => {
  //     item.on_change(
  //       this.datetime_range[0],
  //       this.datetime_range[1],
  //       this.filter_params
  //     );
  //   });
  // }
};
</script>


