<template>
  <el-table-column
    :prop="prop"
    :label="label"
    :column-key="columnKey"
    :filters="l_filters"
    :width="width"
    :formatter="formatter"
    :showOverflowTooltip="showOverflowTooltip"
  >
    <slot />
  </el-table-column>
</template>

<script>
export default {
  name: "dfTableColumn",
  props: {
    url: {
      type: String,
      required: true
    },
    value_field: {
      required: true
    },
    display_field: {
      required: true
    },
    disabled_field: {
      type: String,
      required: false
    },
    type: {
      type: String,
      default: "default"
    },
    results_field: {
      default: "results"
    },
    label: String,
    className: String,
    labelClassName: String,
    property: String,
    prop: String,
    width: {},
    minWidth: {},
    renderHeader: Function,
    sortable: {
      type: [String, Boolean],
      default: false
    },
    sortMethod: Function,
    sortBy: [String, Function, Array],
    resizable: {
      type: Boolean,
      default: true
    },
    context: {},
    columnKey: String,
    align: String,
    headerAlign: String,
    showTooltipWhenOverflow: Boolean,
    showOverflowTooltip: Boolean,
    fixed: [Boolean, String],
    formatter: Function,
    selectable: Function,
    reserveSelection: Boolean,
    filterMethod: Function,
    filteredValue: Array,
    filters: Array,
    filterPlacement: String,
    filterMultiple: {
      type: Boolean,
      default: true
    },
    index: [Number, Function]
  },
  data() {
    return {
      l_filters: []
    };
  },
  mounted() {
    this.get_data();
  },
  methods: {
    get_data() {
      this.$c_master
        .get(this.url)
        .then(response => {
          var results = [];
          var filters = [];
          if (this.results_field) {
            results = response.data[this.results_field];
          } else {
            results = response.data;
          }
          results.forEach(item => {
            filters.push({
              text: Array.isArray(this.display_field)
                ? `${this.display_field.map(key => item[key]).join(" | ")} ${
                    item[this.disabled_field] ? "已禁用" : ""
                  }`
                : `${item[this.display_field]} ${
                    item[this.disabled_field] ? "已禁用" : ""
                  }`,
              value: item[this.value_field]
            });
          });
          this.l_filters = filters;
        })
        .catch(error => {
          this.$message.error(`${display_field} 筛选列表获取失败`);
        });
    }
  }
};
</script>


