<template>
  <div>
    <df-option-bar>
      <el-tooltip content="添加Dashboard">
        <el-button
          size="medium"
          icon="el-icon-plus"
          @click="$df_add_dialog('dfReportCreateUpdateDash')"
        ></el-button>
      </el-tooltip>
      <df-remote-query-select
        v-model="current_dashboard"
