              filterable
              @clear="delete filter_params.filters[item.field_name], on_change()"
            >
              <el-option
                v-for="(ite, i) in item.options"
                :key="i"
                :label="String(ite.label)"
                :value="ite.value===null?'null':ite.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button
              type="primary"
              style="width: 100%"
              @click="filter_params.filters={}; need_change=true"
            >重 置</el-button>
          </el-form-item>
        </el-form>
        <el-button
          slot="reference"
          size="medium"
          :disabled="!Object.keys(filters).length"
        ><i
            class="fa fa-filter"
            aria-hidden="true"
          ></i> {{ Object.keys(filter_params.filters).length }}/{{ Object.keys(filters).length }}</el-button>
      </el-popover>
      <div style="float: right">
        <el-button-group>
          <el-button
            style="margin-left: 0px"
            icon="el-icon-arrow-left"
            size="medium"
            @click="on_prev()"
          ></el-button>
          <el-button
            icon="el-icon-arrow-right"
            size="medium"
            @click="on_next()"
          ></el-button>
        </el-button-group>
        <el-date-picker
          size="medium"
          style="margin-left: 10px"
          :style="{width: date_type_width_map[current_dashboard.time_display_type==undefined?0:current_dashboard.time_display_type]+'px'}"
          v-model="datetime_range"
          :type="date_type_map[current_dashboard.time_display_type==undefined?0:current_dashboard.time_display_type]"
          @change="on_change()"
          :picker-options="current_dashboard.time_display_type==0?picker_options:{shortcuts: []}"
          :clearable="false"
          :default-time="['00:00:00', '23:59:59']"
        ></el-date-picker>
      </div>
    </df-option-bar>
    <div v-loading="loading">
      <div v-if="current_dashboard&&current_dashboard.id">
        <el-tooltip content="添加Row到当前Dashboard">
          <i
            class="fa fa-plus-circle"
            aria-hidden="true"
            @click="$df_add_dialog('dfReportCreateUpdateRow', {dashboard: current_dashboard})"
          ></i>
        </el-tooltip>
        <div style="float: right">
          <el-tooltip content="移除当前Dashboard">
            <i
              class="el-icon-remove"
              aria-hidden="true"
              @click="on_delete()"
            ></i>
          </el-tooltip>
          <el-tooltip
            content="编辑当前Dashboard"
            style="margin-left: 4px"
          >
            <i
              class="fa fa-pencil-square"
              aria-hidden="true"
              @click="$df_add_dialog('dfReportCreateUpdateDash', {type: 'update', item: current_dashboard})"
            ></i>
          </el-tooltip>
        </div>
      </div>
      <df-report-row
        v-for="(item, index) in data"
        :key="index"
        :row="item"
        :datetime_range="datetime_range"
        :filter_params="filter_params"
        ref="rows"
        style="margin-bottom: 20px"
        @delete="on_delete_row(item)"
      >
      </df-report-row>
    </div>
  </div>
</template>


<script>
import moment from "moment";
import list_mixin from "../../utils/mixins/list_mixin";
export default {
  name: "dfReport",
  mixins: [list_mixin],
  data() {
    this.date_type_map = {
      0: "datetimerange",
      1: "daterange",
      2: "monthrange"
    };
    this.date_type_width_map = {
      0: 400,
      1: 260,
      2: 240
    };
    this.picker_options = {
      shortcuts: [
        {
          text: "最近1天",
          onClick(picker) {
            const end = moment()
              .endOf("day")
              .toDate();
            const start = moment()
              .startOf("day")
              .toDate();
            picker.$emit("pick", [start, end]);
          }
        },
        {
          text: "最近7天",
          onClick(picker) {
            const end = moment()
              .endOf("day")
              .toDate();
            const start = moment()
              .subtract(6, "days")
              .startOf("day")
              .toDate();
            picker.$emit("pick", [start, end]);
          }
        },
        {
          text: "最近30天",
          onClick(picker) {
            const end = moment()
              .endOf("day")
              .toDate();
            const start = moment()
              .subtract(29, "days")
              .startOf("day")
              .toDate();
            picker.$emit("pick", [start, end]);
          }
        },
        {
          text: "最近90天",
          onClick(picker) {
            const end = moment()
              .endOf("day")
              .toDate();
            const start = moment()
              .subtract(89, "days")
              .startOf("day")
              .toDate();
            picker.$emit("pick", [start, end]);
          }
        },
        {
          text: "最近365天",
          onClick(picker) {
            const end = moment()
              .endOf("day")
              .toDate();
            const start = moment()
              .subtract(364, "days")
              .startOf("day")
              .toDate();
            picker.$emit("pick", [start, end]);
          }
        }
      ]
    };
    return {
      url: "report/rows",
      full_screen: false,
      dashboards: [],
      params: {
        dashboard: "",
        page_size: 50,
        ordering: "-priority"
      },
      datetime_range: ["", ""],
      need_change: false,
      filters: {},
      filter_params: {
        filters: {}
      },
      // current_dashboard: localStorage.getItem("report_center_current_dashboard")
      //   ? JSON.parse(localStorage.getItem("report_center_current_dashboard"))
      //   : { filters: [] }
      current_dashboard: { filters: [] }
    };
  },
  mounted() {
    this.$el.addEventListener("fullscreenchange", event => {
      console.log("event:", event);
      if (document.fullscreenElement !== null) {
        this.full_screen = true;
      } else {
        this.full_screen = false;
      }
    });
    window.onunload = event => {
      this.save_env();
    };
    if (this.params.dashboard) {
      this.get_data();
    }
    // this.get_dashboards();
  },
  methods: {
    after_get_data() {
      this.on_change();
    },
    on_change() {
      if (this.current_dashboard.time_display_type == 2) {
        this.datetime_range[1] = moment(this.datetime_range[1])
          .endOf("month")
          .toDate();
      }
      this.datetime_range[1].setMilliseconds(999);
      this.get_filters();
      this.need_change = true;
    },
    get_datetime_range() {
      // var report_center_start_time = localStorage.getItem(
      //   "report_center_start_time"
      // );
      // var report_center_end_time = localStorage.getItem(
      //   "report_center_end_time"
      // );
      // var report_center_start_time = null;
      // var report_center_end_time = null;
      // return [
      //   report_center_start_time
      //     ? new Date(report_center_start_time)
      //     : moment()
      //         .subtract(6, "days")
      //         .startOf("day")
      //         .toDate(),
      //   report_center_end_time
      //     ? new Date(report_center_end_time)
      //     : moment()
      //         .endOf("day")
      //         .toDate()
      // ];
    },
    init_datetime_range() {
      this.datetime_range = [
        moment()
          .startOf("month")
          .toDate(),
        moment()
          .endOf("month")
          .toDate()
      ];
    },
    set_datetime_range(range) {
      localStorage.setItem("report_center_start_time", range[0].toISOString());
      localStorage.setItem("report_center_end_time", range[1].toISOString());
    },
    // get_dashboards() {
    //   this.$c_master
    //     .get("graph/dashboards?page_size=100&ordering=-priority")
    //     .then(response => {
    //       this.dashboards = response.data.results;
    //       if (this.dashboards.length && !this.params.dashboard) {
    //         this.params.dashboard = this.dashboards[0].id;
    //         this.current_dashboard = this.dashboards[0];
    //         this.get_data();
    //       }
    //     });
    // },
    get_diff_month_number() {
      var diff_year =
        moment(this.datetime_range[1]).year() -
        moment(this.datetime_range[0]).year();
      var diff_month =
        moment(this.datetime_range[1]).month() -
        moment(this.datetime_range[0]).month();
      return diff_year * 12 + diff_month + 1;
    },
    on_prev() {
      console.log("this.datetime_range", this.datetime_range);
      var datetime_range = ["", ""];
      if (this.current_dashboard.time_display_type == 2) {
        var diff = this.get_diff_month_number();
        datetime_range[0] = moment(this.datetime_range[0])
          .subtract(diff, "months")
          .startOf("month")
          .toDate();
        datetime_range[1] = moment(this.datetime_range[1])
          .subtract(diff, "months")
          .endOf("month")
          .toDate();
      } else {
        var diff =
          moment(this.datetime_range[1]) - moment(this.datetime_range[0]);
        if (this.datetime_range[1].getMilliseconds() == 999) {
          diff += 1;
        }
        datetime_range[0] = moment(this.datetime_range[0])
          .subtract(diff, "ms")
          .toDate();
        datetime_range[1] = moment(this.datetime_range[1])
          .subtract(diff, "ms")
          .toDate();
      }

      this.datetime_range = datetime_range;
      this.on_change();
    },
    on_next() {
      console.log("this.datetime_range");
      var datetime_range = ["", ""];
      if (this.current_dashboard.time_display_type == 2) {
        var diff = this.get_diff_month_number();
        datetime_range[0] = moment(this.datetime_range[0])
          .add(diff, "months")
          .startOf("month")
          .toDate();
        datetime_range[1] = moment(this.datetime_range[1])
          .add(diff, "months")
          .endOf("month")
          .toDate();
      } else {
        var diff =
          moment(this.datetime_range[1]) - moment(this.datetime_range[0]);
        if (this.datetime_range[1].getMilliseconds() == 999) {
          diff += 1;
        }
        datetime_range[0] = moment(this.datetime_range[0])
          .add(diff, "ms")
          .toDate();
        datetime_range[1] = moment(this.datetime_range[1])
          .add(diff, "ms")
          .toDate();
      }
      this.datetime_range = datetime_range;
      this.on_change();
    },
    get_filters() {
      for (let item of this.current_dashboard.filters) {
        this.$c_master
          .post(
            `report/filters/${item.id}/get-data`,
            {},
            {
              params: {
                start_time: this.datetime_range[0],
                end_time: this.datetime_range[1]
              }
            }
          )
          .then(response => {
            if (!Object.keys(this.filters).includes(item.id)) {
              var filters = {};
              filters[item.id] = Object.assign({}, item, {
                options: response.data.results
              });
              // this.filters = Object.assign(this.filters, filters);
              this.filters = Object.assign({}, this.filters, filters);
              return;
            }
            // this.filters[item.id].options = response.data.results;
            this.filters[item.id] = Object.assign({}, item, {
              options: response.data.results
            });
          });
      }
    },
    on_change_dashboard(item) {
      this.data = [];
      this.filter_params.filters = {};
      this.filters = {};
      this.params.dashboard = item.id;
      this.init_datetime_range();
      this.get_data();
    },
    on_full_screen() {
      if (this.full_screen) {
        document.exitFullscreen();
        return;
      }
      var elem = this.$el;
      if (elem.requestFullscreen) {
        elem.requestFullscreen();
      } else if (elem.msRequestFullscreen) {
        elem.msRequestFullscreen();
      } else if (elem.mozRequestFullScreen) {
        elem.mozRequestFullScreen();
      } else if (elem.webkitRequestFullscreen) {
        elem.webkitRequestFullscreen();
      }
    },
    save_env() {
      localStorage.setItem(
        "report_center_start_time",
        this.datetime_range[0].toISOString()
      );
      localStorage.setItem(
        "report_center_end_time",
        this.datetime_range[1].toISOString()
      );
      localStorage.setItem("report_center_dashboard", this.params.dashboard);
      // localStorage.setItem(
      //   "report_center_dashboards",
      //   JSON.stringify(this.dashboards)
      // );
      localStorage.setItem(
        "report_center_current_dashboard",
        JSON.stringify(this.current_dashboard)
      );
    },
    on_delete() {
      const url = `report/dashboards/${this.current_dashboard.id}`;
      this.loading = true;
      this.$c_master
        .delete(url)
        .then(reponse => {
          this.loading = false;
          this.$message.success("已删除");
        })
        .catch(error => {
          this.loading = false;
        });
    },
    on_delete_row(item) {
      this.data.splice(this.data.indexOf(item), 1);
    }
  },
  updated() {
    if (!this.need_change) {
      return;
    }
    this.need_change = false;
    if (!this.$refs.rows) {
      return;
    }
    this.$refs.rows.forEach(item => {
      item.on_change(
        this.datetime_range[0],
        this.datetime_range[1],
        this.filter_params
      );
    });
  },
  beforeDestroy() {
    this.save_env();
  }
};
</script>


