        ></i>
      </div>
    </div>
    <el-table
      style="margin-top: 20px"
      :data="table_data"
      empty-text="没有数据 扩大时间范围试试。。。"
      stripe
      :height="graph.height"
      border
      size="medium"
      v-loading="loading"
      @sort-change="on_change_sort"
      @filter-change="on_change_filter"
      @cell-click="on_cell_click"
    >
      <el-table-column
        fixed
        :key="graph.dimension"
        :prop="graph.dimension"
        :label="graph.dimension"
        min-width="150"
        :formatter="format_row"
        :show-overflow-tooltip="true"
      >
      </el-table-column>
      <el-table-column
        v-for="item in graph.metrics"
        :key="item"
