  methods: {
    format_row(item, column, cell_alue, index) {
      if (typeof cell_alue == "boolean") {
        return String(cell_alue);
      } else if (typeof cell_alue == "object") {
        return JSON.stringify(cell_alue);
      }
      return cell_alue;
    },
    on_cell_click(row, column) {
      if (!this.graph.on_click) {
        console.log("没有配置详情列表 请联系管理员配置");
        return;
      }
      if (column.property == this.graph.dimension) {
        console.log("Dimension 不允许点击");
        return;
      }
      var click_args = {
        dimension: row[this.graph.dimension],
        metric: column.property,
        count: row[column.property],
        item_data: row
      };
      var title = `${this.graph.dimension}&&${column.property}`;
      this.$emit("cell-click", click_args, title);
