<template>
  <el-dialog
    title="最新动态"
    :visible.sync="is_visible"
    width="700px"
    @close="on_close"
  >
    <div style="padding: 0px 40px; max-height: 360px; overflow: scroll">
      <el-steps
        class="custome_steps"
        direction="vertical"
      >
        <el-step
          v-for="(version, index) in update_logs"
          :key="index"
          status="finish"
        >
          <span
            slot="title"
            style="font-size: 20px; color: #515a6e; font-weight: 360;"
          >{{version.version}}</span>
          <template slot="icon">
            <!-- <i
            class="fa fa-circle-thin"
            aria-hidden="true"
            style="color: #62A5F3"
          ></i> -->
            <i>&nbsp;</i>
          </template>
          <!--#62A5F3-->
          <div slot="description">
            <p><code style="background-color: #F7F7FE; font-size: 14px; color: #666666; padding: 1px 5px; border: 1px solid rgb(238, 238, 238); border-radius: 3px">{{ version.time }}</code></p>
            <ul
              type="circle"
              style="font-size: 14px; line-height: 21px; color: #515a6e"
            >
              <li
                v-for="(item, i) in version.items"
                :key="i"
              ><span v-html="item"></span></li>
            </ul>
          </div>
        </el-step>
      </el-steps>
    </div>
  </el-dialog>
</template>

<script>
import { dialog_mixin } from "../../utils/mixins/index";
import base from "../base"
export default {
  name: "dfUpdateLog",
  extends: base,
  mixins: [dialog_mixin],
  props: {
    /* 格式如下 items 可以为html 如 a 标签实现跳转
      [
        {
          "version": "2.5.2",
          "time": "2018-12-05",
          "items": [
              `分类报表->原因分类新增点击图块列出对应的事件列表功能 <a href="${prot_host_port}/#/app/class-report/cause-class">原因分类报表</a>`,
              "新增打回功能 如果事件处理有问题 管理员可在'管理员->所有事件/我记录的'中点击打回 此时事件回到待处理状态"
          ]
        },
        {
            "version": "2.5.1",
            "time": "2018-12-04",
            "items": [
                `增加 <a href="${prot_host_port}/#/app/class-report/cause-class">原因分类报表</a>`,
                "增加更新日志功能 当系统有更新时 用户打开系统将会自动弹出最新更新日志dialog 也可点击右上角‘更新日志’按钮查看"
            ]
        }
      ]
    */
    update_logs: {
      type: Array,
      required: true
    },
    // 是否检查有新版本 如果开启创建组件回去检查是否有新版本  如果有才显示 
    check_new: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      is_visible: false
    };
  },
  mounted() {
    if (!this.check_new) {
      this.is_visible = true;
      return;
    }
    const last_version = localStorage.getItem("last_version");
    if (
      this.update_logs.length &&
      last_version != this.update_logs[0].version
    ) {
      localStorage.setItem("last_version", this.update_logs[0].version);
      this.is_visible = true;
      return
    }
    this.on_close()
  },
  methods: {}
};
</script>


