<template>
  <el-input
    class="c-search"
    style="float: right; width: 300px"
    size="medium"
    v-model="inner_value"
    :autofocus="true"
    :disabled="disabled"
    clearable
    @clear="on_clear"
    @keyup.enter.native="on_change()"
    :placeholder="placeholder"
  >
    <el-button
      class="df-theme-color"
      @click="on_change()"
      slot="append"
    >搜索</el-button>
  </el-input>
</template>

<script>
import base from "../base";
export default {
  name: "dfSearch",
  extends: base,
  props: {
    value: {
      type: String
    },
    placeholder: {
      required: false,
      default: ""
    },
    disabled: {
      type: Boolean
    }
  },
  data() {
    return {
      inner_value: this.value
    };
  },
  methods: {
    on_change() {
      this.$emit("change", this.inner_value);
    },
    on_clear() {
      this.$emit("input", "");
      this.$emit("change", "");
    }
  },
  watch: {
    inner_value(new_value, old_value) {
      this.$emit("input", new_value);
    },
    value(new_value, old_value) {
      this.inner_value = this.value;
    }
  }
};
</script>

<style>
.c-search ~ .el-input-group__append {
  background-color: #62a5f3 !important;
  color: #ffffff !important;
}
</style>




